*{
    margin:0px;
    padding: 0px;
} 
body{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -10;
    background-image: url('https://images.unsplash.com/photo-1533035336122-4327d347d2fe?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=2670&q=80');
}
.Box{
    display: flex;
    flex-direction: column;
    /* justify-content: ce; */
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -10;
    background-image: url("https://images.unsplash.com/photo-1533035353720-f1c6a75cd8ab?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80");
    box-shadow: 0 0 5px 2px #282a2d;
    line-height: 1.4;
    width: 300px;
    height: 80vh;
}

.todoList{
    /* background-color: black; */
    color: rgb(52, 6, 6);
    font-weight: bold;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.todo{
    display: flex;
    justify-content: space-between;
    padding:2px
}
.todo button:hover{
    transform: scale(1.2);
    color: white;
    background-color: black;
    border-radius: 5px;

}
.box button:hover{
    transform: scale(1.2);
}
.todo span{
    font-weight: bold;
}
 